import Axios from 'axios'
import { actionType } from '@/assets/js/actionType'

const state = {
  article: {
    contType: null,
    cover: '',
    clubId: -1,
    title: '',
    desc: '',
    content: ''
  },
  post: {
    part: -1,
    content: '',
    images: [],
    clubId: -1
  }
}
const getters = {}
const mutations = {
  [actionType.write.set_article_data] (state, article = {cover, clubId, title, desc, content}) {
    Object.assign(state.article, article)
  },
  [actionType.write.set_post_data] (state, post = {part, content, images, clubId}) {
    Object.assign(state.post, post)
  }
}
const actions = {}

export default {
  state,
  getters,
  actions,
  mutations
}



