// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'
import store from '../src/store'
import { Swipe, SwipeItem, Cell, Button, Actionsheet, InfiniteScroll, Popup, Field, Checklist } from 'mint-ui'
import '../src/assets/reset.css'
import 'mint-ui/lib/style.css'
import '../src/assets/iconfont/iconfont.css'
import '../src/assets/global.scss'
import { actionType } from '@/assets/js/actionType'
import port from '@/assets/js/port'

Vue.component(Swipe.name, Swipe)
Vue.component(SwipeItem.name, SwipeItem)
Vue.component(Cell.name, Cell)
Vue.component(Button.name, Button)
Vue.component(Actionsheet.name, Actionsheet)
Vue.component(Field.name, Field)
Vue.component(Checklist.name, Checklist)
Vue.use(InfiniteScroll)
Vue.component(Popup.name, Popup)

Vue.config.productionTip = false

Vue.prototype.$http = axios
Vue.prototype.$qs = qs
Vue.prototype.$actionType = actionType
Vue.prototype.$port = port

// axios返回拦截
axios.defaults.crossDomain = true;
axios.defaults.withCredentials = true;
axios.interceptors.response.use(res => {
    return res.data
}, err => {
    console.log(err)
    return Promise.reject(err)
});
// 获取系统配置
axios.get(port.GET_CONFIG).then(({ code, data, msg }) => {
        if (code === 200) {
            store.commit(actionType.config.set_data, { data })
        }
    })
    /* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    axios,
    components: { App },
    template: '<App/>'
})


