export default [
  {
    path: '/club',
    component: resolve => require(['@/views/club.vue'], resolve),
  },
  {
    path: '/coach',
    component: resolve => require(['@/views/coach.vue'], resolve)
  },
  {
    path: '/auth/club',
    component: resolve => require(['@/views/auth/club.vue'], resolve)
  },
  {
    path: '/auth/coach',
    component: resolve => require(['@/views/auth/coach.vue'], resolve)
  },
  {
    path: '/auth/player',
    component: resolve => require(['@/views/auth/player.vue'], resolve)
  }
]



